<template>
  <div class="logistic-container">
    <ul class="steps clearfix">
      <li class="active"><i class="arrow-blue"></i>1、选择类目</li>
      <li>2、填写产品信息</li>
    </ul>
    <section class="cate-box">
      <div class="cate-list">
        <ul>
          <li class="cate-item" v-for="(data, index) in cateone">
            <a href="javascript:;" @click="chose(0, data.id, data.name)" :class="{'selected': cateoneId==data.id}">
              {{data.name}}
              <i class="icon-arright"></i>
            </a>
          </li>
        </ul>
      </div>
      <div class="cate-list">
        <ul>
          <li class="cate-item" v-for="(data, index) in catetwo">
            <a href="javascript:;" @click="chose(1, data.id, data.name)" :class="{'selected': catetwoId==data.id}">
              {{data.name}}
              <i class="icon-arright"></i>
            </a>
          </li>
        </ul>
      </div>
      <div class="cate-list">
        <ul v-if="choseOne">
          <li class="cate-item" v-for="(data, index) in catethree">
            <a href="javascript:;" @click="chose(2, data.id, data.name)" :class="{'selected': catethreeId==data.id}">
              {{data.name}}
            </a>
          </li>
        </ul>
      </div>
    </section>
    <el-button type="primary" class="size-l" @click="next">下一步</el-button>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ProductCategory',
  props: {
  },
  data () {
    return {
      selectedSubNav: 2,
      cateoneId: '',
      catetwoId: '',
      catethreeId: '',
      catename: [],
      choseOne: false
    }
  },
  components: {},
  methods: {
    chose (index, parentId, name) {
      if (index === 0) {
        this.$store.dispatch('getCate2', {parentId: parentId})
        this.cateoneId = parentId
        this.catename[0] = name
        this.choseOne = false
      } else if (index === 1) {
        this.$store.dispatch('getCate3', {parentId: parentId})
        this.catetwoId = parentId
        this.catename[1] = name
        this.choseOne = true
      } else {
        this.catethreeId = parentId
        this.catename[2] = name
      }
    },
    next () {
      // 三个等级类目全选
      let catelen = this.catename.length
      if (catelen > 1) {
        let name = [this.cateoneId, this.catetwoId, this.catethreeId]
        if (catelen === 2) {
          if (this.catethree.length) {
            this.$message({
              type: 'info',
              message: '请选择类目'
            })
            return
          }
          name = [this.cateoneId, this.catetwoId]
        }
        name = name.join('>')
        for (let i = 0; i < catelen; i++) {
          this.catename[i] = this.catename[i].split('/').join('-')
        }
        let rout = ['/ProductModify', name, this.catename.join('>'), 0].join('/')
        this.$router.push({path: rout})
      } else {
        this.$message({
          type: 'info',
          message: '请选择类目'
        })
        return
      }
    }
  },
  watch: {
  },
  computed: mapGetters({
    cateone: 'cateone',
    catetwo: 'catetwo',
    catethree: 'catethree'
  }),
  created () {
    this.$store.dispatch('getCate')
  }
}
</script>
<style lang='less'>
@import '../../assets/style/variable.less';
/*@import '../../assets/style/product.less';*/
@lightblue: #edf3fe;
.steps{
  margin-bottom: 20px;
  li{
    position: relative;
    float: left;
    width: 50%;
    height: 44px;
    line-height: 44px;
    font-size: 16px;
    background-color: @lightblue;
    color: @mainFontColor;
    text-align: center;
  }
  .active{
    background-color: @activeBlue;
    color: @white;
    border-radius: 5px;
  }
  .arrow-blue, .arrow-light{
    position: absolute;
    right: 0;
    width: 13px;
    height: 100%;
    background-image: url(data:image/png;base64,);
    z-index: 99;
  }
  .arrow-light{
    right: -4px;
    width: 10px;
    background-image: url(data:image/png;base64,);
  }
}
.cate-box{
  display: box;
  display: -webkit-box;
  border-bottom: 1px solid #DFE3EB;
  border-top: 1px solid #DFE3EB;
}
.cate-list{
  height: 600px;
  box-flex: 1;
  -webkit-box-flex: 1;
  border-right: 1px solid #DFE3EB;
  overflow-y: auto;
  ul{
    margin: 10px 0;
  }
}
.cate-item{
  position: relative;
  height: 32px;
  line-height: 32px;
  .icon-arright{
    position: absolute;
    right: 10px;
    top: 1px;
  }
  a{
    display: block;
    padding-left: 10px;
    color: @textColor;
    &:hover{
      /*background-color: #EAF0F6;*/
      background-color: #e5f7f9;
    }
    &.selected{
      /*border-left: 2px solid @activeBlue;*/
      /*background-color: #dde7fe;*/
      font-weight: bold;
      color: #00B8CC;
      background-color: #e5f7f9;
    }
  }
}
.size-l{
  float: right;
  margin: 20px;
}
</style>



